.detailedPane {
  width: 100%;
  height: auto;
  background-color: blue;
  position: relative;

}

.closeIcon {
  position: absolute;
  right: 2rem;
  top: 1rem;
  cursor: pointer;
  z-index: 1;
  background-color: rgba(0, 0, 0, 1);
  width: 4.2rem;
  height: 4.2rem
}

.detailedPane img {
    position: relative;
  object-fit: cover;
  width: 100%;
  height: auto;
}
