.tile {
  position: relative;
  display: inline-block;
  width: calc(100% / 6 - 10px);
  box-sizing: border-box;
  height: 150px;
  margin-right: 10px;
  font-size: 20px;
  cursor: pointer;
  -webkit-transition: 450ms all;
  transition: 450ms all;
  -webkit-transform-origin: center right;
  transform-origin: center left;
  &__media {
    height: 150px;
  }
  &:hover {
    margin: 0 0px 0 0px;
    transform: scale(2);
    .tile__details {
      opacity: 1; // khi hover thi thang con sao - quan he cha - con
    }
    ~ .tile {
      -webkit-transform: translate3d(
        40rem,
        0px,
        0
      ); // khi hover cai nay thi nhung cai kha cung bi anh huong - quan he dong cap
      transform: translate3d(31.5rem, 0px, 0);
    }
  }
  &--active{
    border: 3px solid #fff;
    box-sizing: content-box;
  }
}

